.presentation {
  margin: 0 auto;
  width: 80%;
}
.toggle-mode {
    text-align: center;
    cursor: pointer;
    width: 100%;
    display: block;
    text-decoration: none;
    color: black;
    padding-top: 10px;
    padding-bottom: 10px;
}

.presentation .action {
    padding: 10px;
    width: 100px;
    margin-left: 15px;
    text-align: center;
    color: white;
    border-radius: 0.25em;
    cursor: pointer;
}

.presentation .action.working {
  background-color: #28a745;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#34d058), color-stop(90%, #28a745));
  background-image: -webkit-linear-gradient(top, #34d058 0%, #28a745 90%);
  background-image: -o-linear-gradient(top, #34d058 0%, #28a745 90%);
  background-image: linear-gradient(-180deg, #34d058 0%, #28a745 90%);
}

.presentation .action.complete {
    background: #5252ff;
}

.presentation-toolbar {
    display: -ms-flexbox;
    display: flex;
}

.presentation-toolbar .back {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    cursor: pointer;
}

.current-step {
    line-height: 38px;
    -ms-flex-positive: 1;
        flex-grow: 1;
    max-width: 100px;
    text-align: center;
}

.presentation-header {
    font-size: 35px;
    text-align: center;
}


