.workshop-single {
  max-width: 500px;
  -ms-flex-positive: 1;
      flex-grow: 1;
  border: 1px solid #dadada;
  border-radius: 4px;
  text-align: left;
  cursor: pointer;
  padding: 15px;
  position: relative;
  flex-grow: 1;
  width: 100%;
  margin-bottom: 15px;
}

a.edit-workshop {
    position: absolute;
    right: 20px;
    top: 20px;
    color: #2fbb2f;
}

.workshop-date {
    font-size: 12px;
    color: #a7a6a5;
    font-weight: 100;
}

a.present-workshop {
    position: absolute;
    right: 50px;
    top: 20px;
}


